import React from 'react'
import { Input, Row, Col, Button, Form, Card, message } from 'antd'

class Bmi extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
          bmi:'',
          level:'',
        }
    }

    handleSubmit = (e) => {
      e.preventDefault()
      // console.log('', this.props.form.getFieldsValue());
		this.props.form.validateFields((err, values) => {
			if (err) {
				message.error('')
				return
			}
			let w = this.props.form.getFieldsValue().weight
			let h = this.props.form.getFieldsValue().height
			let bmi = ( w / ((h / 100) ** 2)).toFixed(1)
			let level
			if (bmi < 18.5) {
				level = 0
			} else if(bmi >= 18.5 && bmi <= 24.9) {
				level = 1
			} else if(bmi === 25) {
				level = 2
			} else if(bmi >= 25 && bmi <= 29.9) {
				level = 3
			} else if(bmi >= 30) {
				level = 4
			}
			this.setState({ bmi:bmi, level:level })
		})
  }

    handleReset = (e) => {
        e.preventDefault()
        this.props.form.resetFields()
        this.setState({ bmi:'', level:'' })
    }

  	render() {
  		const { getFieldDecorator } = this.props.form
  		let i = this.state.level
  	  	return (
  	  		<div style={{marginTop:80}}>
  	  			<Form onSubmit={this.handleSubmit}>
  	  				<Row type="flex" justify="center" className="rowItem">
	  	  				<Col span={10}>
									{getFieldDecorator('weight', {
										rules: [{ required: true }],
									})(
											<Input type="number" addonBefore="" addonAfter="kg" />
									)}
	  	  				</Col>
  	  				</Row>
  	  				<Row type="flex" justify="center" className="rowItem">
	  	  				<Col span={10}>
									{getFieldDecorator('height', {
										rules: [{ required: true }],
									})(
											<Input type="number" addonBefore="" addonAfter="cm" />
									)}
	  	  				</Col>
	  	  			</Row>
	  	  			<Row type="flex" justify="center" className="rowItem">
	  	  				<Col span={10}>
		  	  				<Button style={{width:'50%'}} htmlType="submit"></Button>
		  	  				<Button style={{width:'50%'}} onClick={this.handleReset}></Button>
	  	  				</Col>
	  	  			</Row>
	  	  		</Form>
	  	  		<Row type="flex" justify="center" className="rowItem">
	  	  			<Col span={10}>
	  	  				<Input addonBefore="BMI" value={this.state.bmi} id="red" disabled/>
	  	  			</Col>
	  	  		</Row>
	  	  		<Row type="flex" justify="center" className="rowItem">
  	  				<Col span={10}>
  	  					<Card bodyStyle={{ padding:0}} id="cardWrap">
									<p className={i===0?'activeP':'die'}>&nbsp;&nbsp;&nbsp;&nbsp;{'<18.5'}</p>
									<p className={i===1?'activeP':'die'}>&nbsp;&nbsp;&nbsp;&nbsp;{'18.524.9'}</p>
									<p className={i===2?'activeP':'die'}>&nbsp;&nbsp;&nbsp;&nbsp;{'=25'}</p>
									<p className={i===3?'activeP':'die'}>&nbsp;&nbsp;&nbsp;&nbsp;{'25.029.9'}</p>
									<p className={i===4?'activeP':'die'}>&nbsp;&nbsp;&nbsp;&nbsp;{'30.034.9'}</p>
  	  					</Card>
  	  				</Col>
  	  			</Row>
						<Row type="flex" justify="center" className="rowItem">
	  	  			<Col span={10}>
								<div className="issue"><a href="https://github.com/MuYunyun/react-antd-demo/issues" target="_blank" rel='noopener noreferrer'>issue</a>O(_)O</div>
	  	  			</Col>
	  	  		</Row>
	  	  	</div>
  	  	)
  	}
}

Bmi = Form.create()(Bmi)
export default Bmi

